*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-accent: #f8d24e;
  --main-white: #fcfcfc;
  --secondary-white: #ababab;
  --main-dark: #131313;
  --main-green: #84a178;
  --main-dark-green: #0a8817;
  --main-red: #d6936d;
  --main-grey-footer: #7a7a7a;
  --main-white-hover: #fff;
  --secondary-font: "Seymour One", sans-serif;
}

.visually-hidden {
  width: 1px;
  height: star1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--main-white);
  cursor: url("cursor-black.5374b5cc.png") 15 12, auto;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

a {
  color: inherit;
  cursor: url("cursor-green.62b8bd01.png") 15 12, auto;
  text-decoration: none;
}

button {
  cursor: pointer;
  cursor: url("cursor-green.62b8bd01.png") 15 12, auto;
  border: none;
  font-family: inherit;
  display: block;
}

.slick-dots li button:before {
  font-size: 20px;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  list-style: none;
}

.title {
  font-family: var(--secondary-font);
  letter-spacing: .04em;
  text-transform: uppercase;
  text-align: center;
  font-size: 30px;
  line-height: 1.27;
}

@media screen and (min-width: 768px) {
  .title {
    letter-spacing: .06em;
    font-size: 34px;
    line-height: 1.32;
  }
}

@media screen and (min-width: 1200px) {
