  background-color: var(--main-white);
  border-radius: 10px;
  padding: 20px;
  position: absolute;
  top: 53%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 479px) {
  .modal__product {
    width: 90%;
    height: 100%;
  }
}

@media screen and (max-width: 767px) {
  .modal__product {
    width: 80%;
